@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap);
body {
  margin: 0;
  padding: 0;
  background: #77a1d3;
  background: linear-gradient(to left, #e684ae, #79cbca, #77a1d3);
}

h2 {
  font-family: "Caveat", cursive;
  margin: 0px auto;
}
p {
  font-family: fantasy;
  font-weight: bold;
  margin: 0px auto;
}

img {
  width: 350px;
  height: 350px;
  border: 2px solid white !important;
}

:root {
  --time: 0.707s;
}

.cards {
  display: inline-block;
}

.card {
  perspective: 40rem;
}

.card-body {
  display: flex;
  transform-style: preserve-3d;
  transition: 0.707s transform;
  transition: var(--time) transform;
}
.card:hover .card-body,
.card:focus .card-body {
  transform: rotateX(-180deg);
}

.card-front,
.card-back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  min-width: 100%;
  display: grid;
  flex-direction: column !important;
  align-items: flex-start !important;
}

.card-back {
  transform: rotateX(-180deg) translate(-100%, 0);
}

/* Make it Pretty */
body {
  line-height: 1.3;
  font-family: sans-serif;
}

.cards {
  grid-gap: 1.5rem;
  grid-auto-rows: 2fr;
  padding: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 2fr));
  width: 350px !important;
}

.card {
  display: flex;
  transition: z-index, transform calc(0.707s / 4);
  transition: z-index, transform calc(var(--time) / 4);
  transition-delay: 0.707s, 0s;
  transition-delay: var(--time), 0s;
  text-decoration: none;
  color: inherit;
  z-index: 0;
}
.card:hover {
  transition-delay: 0s;
  z-index: 1;
}
.card:active {
  transform: scale(0.975);
}

.card-body {
  border-radius: 0.25rem;
  flex: 1 1;
}

.card-front,
.card-back {
  display: flex;
  align-items: center;
  background-color: white;
  box-sizing: border-box;
  padding: 8px;
  border-radius: 0.25rem;
}

.card-front {
  font-size: 1rem;
}

.shadow,
.card-front,
.card-back {
  box-shadow: 0 -1px 1px rgba(0, 0, 0, 0.04), 0 2px 2px rgba(0, 0, 0, 0.04),
    0 4px 4px rgba(0, 0, 0, 0.04), 0 8px 8px rgba(0, 0, 0, 0.04),
    0 16px 16px rgba(0, 0, 0, 0.04);
}

@font-face {
  font-family: "Ellenoire Regular";
  font-style: normal;
  font-weight: normal;
  src: local("Ellenoire Regular"), url(/SCIENTISTS/static/media/Ellenoire.bf7586e1.woff) format("woff");
}
h1 {
  font-family: "Ellenoire Regular";
  color: white;
  text-decoration: underline;
  font-size: 48px !important;
}

* {
  font-family: monospace;
}
body{
  margin: "10px";
  
}

